/* Estilos del banner */

#banner {
    background-image: linear-gradient(to right, #98251c,#c5452b);
    color: #f6f4ef !important;
    padding-top: 3%;
}

.titulo-atractivo {
    font-family: 'Nunito Sans', sans-serif;
    font-size: 30px;
    font-weight: 500;
}

.img-banner {
    height: 400px;
    padding-left: 150px;
    padding-bottom: 30px;
    border-radius: 5px;
}

.efectobanner {
    width: 100%;
}